.CatalogueItem {
    display: inline-block;
    margin: 0 25px 50px 25px;
    width: 224px;
    height: 264px;
    background: #2F2D2E;
    border-radius: 30px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14);
    transition: border, box-shadow .3s;
    text-align: center;
}

.CatalogueItem:hover {
    border: 1px solid #aaa;
    box-shadow: 0 4px 15px 0 rgba(0, 0, 0, .25);
}

.Photo {
    display: block;
    margin: 0 auto 10px auto;
    padding-top: 23px;
}

.Info {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 10px;
}

.Info img {
    width: 38px;
    height: 37px;
}

.TitleItem {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    margin: auto auto;
    height: 36px
}

@media (max-width: 1100px) {
    .CatalogueItem {
        margin: 0 25px 50px 25px;
        width: 200px;
        height: 240px;
    }

    .Photo {
        margin: 0 auto 10px auto;
        padding-top: 23px;
    }

    .Info {
        margin-top: 10px;
    }

    .Info img {
        width: 38px;
        height: 37px;
    }

    .TitleItem {
        height: 36px
    }
}

